// Cart
.product-cart {
  padding-bottom: 80px;
  &_top {
    text-align: center;
    margin-bottom: 80px;
    h1 {
      font-size: 48px;
      line-height: 58px;
      margin-bottom: 40px; }
    .cart-progress {
      display: flex;
      justify-content: center;
      .progress-item {
        .number {
          display: inline-block;
          min-width: 56px;
          line-height: 54px;
          border-radius: 50px;
          text-align: center;
          border: 1px solid #DBDBDB;
          margin-right: 24px;
          color: #DBDBDB; }
        .text {
          color: #DBDBDB; }
        &.active {
          .number {
            border-color: $primary;
            color: $primary; }
          .text {
            color: $primary; } }
        & + .progress-item {
          margin-left: 430px;
          position: relative;
          &:before {
            content: "";
            width: 300px;
            height: 1px;
            background: #000;
            position: absolute;
            top: calc(50% - 1px);
            right: calc(100% + 64px); } } } } } }

